import React, {useEffect} from 'react';
import {Redirect, Route, Switch, Link} from 'react-router-dom';
import {makeStyles} from '@mui/styles';
import {Button, Typography} from '@mui/material';
import {Header} from './components';
import {Auth, Calls} from './pages';
import {useAppDispatch, useAppSelector} from "./hooks/redux";
import {authSlice} from "./store/auth/auth.slice";

const useStyles = makeStyles(({
  wrapper: {
    width: 'auto'
  },
  container: {
    width: '100%',
    maxWidth: '1280px',
    margin: '0 auto'
  }
}));

const App = () => {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const isAuth = useAppSelector(state => state.auth.isAuth);
  useEffect(() => {
    // @ts-ignore
    const token = JSON.parse(localStorage.getItem('token'));
    if (token) {
      dispatch(authSlice.actions.setAuth(true));
    }
  });

  return (
    <div className={classes.wrapper}>
      <Switch>

        {/* ÐÐ²ÑÐ¾ÑÐ¸Ð·Ð°ÑÐ¸Ñ */}
        <Route exact path="/auth">
          <Auth/>
        </Route>
        {!isAuth && <Redirect to="/auth"/>}

        {/* ÐÐ²Ð¾Ð½ÐºÐ¸ */}
        <Route path="/calls">
          <Header/>
          <div className={classes.container}>
            <Calls/>
          </div>
        </Route>

        {/* ÐÑÑÑÐµÑÑ */}
        <Route path="/report">
          <Header/>
          <div className={classes.container}>
            <Typography style={{textAlign: 'center', color: 'rgba(0, 0, 0, 0.2)'}} variant="h3">report</Typography>
          </div>
        </Route>

        {/* ÐÐ°Ð³ÑÑÐ·Ð¸ÑÑ Ð·Ð²Ð¾Ð½Ð¾Ðº */}
        <Route path="/upload">
          <Header/>
          <div className={classes.container}>
            <Typography style={{textAlign: 'center', color: 'rgba(0, 0, 0, 0.2)'}} variant="h3">load call</Typography>
          </div>
        </Route>

        {/* ÐÐ¿Ð¾Ð²ÐµÑÐµÐ½Ð¸Ðµ */}
        <Route path="/dictionaries">
          <Header/>
          <div className={classes.container}>
            <Typography style={{textAlign: 'center', color: 'rgba(0, 0, 0, 0.2)'}} variant="h3">alert</Typography>
          </div>
        </Route>

        {/* ÐÐ°ÑÑÑÐ¾Ð¹ÐºÐ¸ */}
        <Route path="/settings">
          <Header/>
          <div className={classes.container}>
            <div>
              <Typography style={{textAlign: 'center', color: 'rgba(0, 0, 0, 0.2)'}} variant="h3">Settings</Typography>
            </div>
          </div>
        </Route>

        <Route exact path="/">
          <Redirect to="calls"/>
        </Route>

        <Route exact path="*">
          <Button variant="contained"><Link to="/">ÐÐ° Ð³Ð»Ð°Ð²Ð½ÑÑ ÑÑÑÐ°Ð½Ð¸ÑÑ.</Link></Button>
          <h1>404</h1>
        </Route>

      </Switch>
    </div>
  );
}

export default App;
